<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--  -->
      <el-button plain icon="el-icon-back" style='margin-bottom: 15px;font-size: 16px;' @click='$router.go(-1)'></el-button>
      <div class="clearfix">
        <el-row style='display: flex!important;align-items: center!important;' :gutter="20">
          <el-col style='display: flex!important;align-items: center!important;' :span="2">
            <h3></h3>
          </el-col>
          <el-col :span="8">
            <el-date-picker
              v-model="date"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder="">
            </el-date-picker>
          </el-col>
          <el-col :span="3"><el-button type="primary" size="small" icon="el-icon-search" @click='search'></el-button></el-col>
          <el-col :span="3"><el-button type="danger" size="small" @click='clearSearch'></el-button></el-col>
        </el-row>
      </div>
      <h3></h3>
      <!-- table -->
      <el-table :data="historyList" border stripe :header-cell-style="{background:'#eee',color:'#606266',fontSize: '14px'}">
        <el-table-column type="index" align="center"></el-table-column>
        <el-table-column label="" prop="dwmc" align="center"></el-table-column>
        <el-table-column label="" prop="gjjzh" width="80px" align="center"></el-table-column>
        <el-table-column label="" prop="lxr" width="100px" align="center"></el-table-column>
        <el-table-column label="" prop="lxdh" width="150px" align="center"></el-table-column>
        <el-table-column label="" prop="jjcontent" align="center"></el-table-column>
        <el-table-column label="" prop="lsh" align="center"></el-table-column>
        <el-table-column label="" prop="num" width="100px" align="center"></el-table-column>
        <el-table-column label="" prop="sqrq" width="200px" align="center"></el-table-column>
        <el-table-column label="" prop="sqstatus" align="center"></el-table-column>
        <el-table-column label="" prop="sqtype" align="center"></el-table-column>
        <el-table-column label="" prop="zjje" align="center"></el-table-column>
        <el-table-column  label="" align="center">
          <template v-slot="{row}">
            <el-button v-if="row.sqstatus == ''" :key='1' disabled type="success" icon="el-icon-check" size="mini" @click='test'>...</el-button>
            <el-button  v-else-if="row.sqstatus == ''"  :key='2' type="primary" icon="el-icon-view" size="mini" @click="downloadPdf(row)"></el-button>
            <el-button  v-else-if="row.sqstatus == ''" type="warning" :key='3' icon="el-icon-edit" size="mini" @click="editApply(row)"></el-button>
            <el-button v-else disabled type="danger"  :key='4' icon="el-icon-close" size="mini"></el-button>
<!--            <el-button v-show="row.sqstatus == ''" disabled type="success" icon="el-icon-check" size="mini" @click='test'>...</el-button>-->
<!--            <el-button  v-show="row.sqstatus == ''"  type="primary" icon="el-icon-view" size="mini" @click="downloadPdf(row)"></el-button>-->
<!--            <el-button  v-show="row.sqstatus == ''" type="warning" icon="el-icon-edit" size="mini" @click="editApply(row)"></el-button>-->
<!--            <el-button v-show="row.sqstatus == ''" disabled type="danger" icon="el-icon-close" size="mini"></el-button>-->
          </template>
        </el-table-column>1
<!--          <template slot-scope="scope">-->
<!--            {{scope.row.add_time | dateFormat}}-->
<!--          </template>-->
<!--        </el-table-column>-->
<!--        <el-table-column label="" width="130px">-->
<!--          <template slot-scope="scope">-->
<!--            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>-->
<!--            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeById(scope.row.goods_id)"></el-button>-->
<!--          </template>-->
<!--        </el-table-column>-->
      </el-table>

      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pageNum" :page-sizes="[5, 10, 15, 20]" :page-size="queryInfo.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total" background>
      </el-pagination>
    </el-card>

    <!--    -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="60%" @close="btnCancel">
      <h1></h1>
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="120px">
        <el-form-item label=":" prop="zjje">
          <el-input v-model="editForm.zjje"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="lxr">
          <el-input v-model='editForm.lxr'></el-input>
        </el-form-item>
        <el-form-item label=":" prop="lxdh">
          <el-input v-model='editForm.lxdh'></el-input>
        </el-form-item>
      </el-form>
      <!--      -->
      <h1></h1>
      <el-table :data="fileInformation.Imports" style="width: 100%" border>
        <el-table-column prop="ImportName" label="" align='center'>
        </el-table-column>
        <el-table-column label="" align='center'>
          <template v-slot="{ row }">
            <el-tag v-if="row.IsUpload" type="success"></el-tag>
            <el-tag v-else type="info"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" align='center'>
          <template slot-scope="scope">
            <el-upload
              class="upload-demo"
              :ref="'uploadRef' + scope.$index"
              action=""
              :limit="1"
              :before-remove="beforeRemove"
              :on-exceed="handleExceed"
              :auto-upload="false">
              <el-button slot="trigger" size="small" type="text"></el-button>
              <el-button style="margin-left: 10px;" size="small" type="text" @click="submitUpload(scope.row.ImportType, scope.$index)"></el-button>
              <el-button size="small" type="text" @click="preview(scope.$index)"></el-button>
            </el-upload>
          </template>
        </el-table-column>
      </el-table>
      <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="btnOk"> </el-button>
        </span>
    </el-dialog>
    <!--  -->
    <div class='prePic' v-show="previewIsShow" >
      <i class="el-icon-close" @click="previewIsShow = false"></i>
      <el-image
        :z-index="2008"
        style="width: 100px; height: 100px"
        :src="previewUrl"
        :preview-src-list="previewUrlList">
      </el-image>
    </div>

  </div>
</template>

<script>
import toBlob from '@/utils/toblob.js'
// 0 1 2 3
export default {
  data() {
    return {
      // 
      queryInfo: {
        gjjzh: sessionStorage.getItem('gjjzh'),
        startDate: '',
        endDate: '',
        pageNum: 1, // 
        pageSize: 10 // 
      },
      // 
      historyList: [],
      // 
      total: 0,
      date: '',
      // 
      editForm: {
        lxdh: '',
        lxr: '',
        zjje: 0
      },
      editFormRules: {
        zjje: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^([1-9]\d{0,9}|0)(\.\d{1,2})?$/, message: '', trigger: 'blur' }
        ],
        lxr: [
          { required: true, message: '', trigger: 'blur' }
        ],
        lxdh: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^1[3-9]\d{9}$/, message: '', trigger: 'blur' }
        ]
      },
      editDialogVisible: false,
      isUpload: false, // 
      fileInformation: {
        BusiName: '',
        Busiid: '',
        ChannelCode: '',
        Imports: [], // 
        SeviceCode: ''
      }, // 
      previewIsShow: false, // 
      previewUrlList: [], // 
      previewUrl: '' // 
    }
  },
  created() {
    this.getHistoyList()
    this.gjjzh = window.sessionStorage.getItem('gjjzh')
  },
  methods: {
    test() {
      alert('click')
    },
    // 
    async getHistoyList() {
      const { data: res } = await this.$http.get('/qtzj/qureyZjqrlist', {params: this.queryInfo})
      console.log('',res)
      if (res.code !== 200) {
        return this.$message.error('')
      }
      if (res.data.total === 0) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.historyList = res.data.records
      this.total = res.data.total
    },
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getHistoyList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getHistoyList()
    },
    async search() {
      // date,2
      if (!this.date) return this.$message.warning(' ')
      this.queryInfo.startDate = this.date[0].getFullYear() + '-' + ((this.date[0].getMonth() + 1 + '') .padStart(2, '0')) + '-' + ((this.date[0].getDate()+ '').padStart(2, '0'))
      this.queryInfo.endDate = this.date[1].getFullYear() + '-' + ((this.date[1].getMonth() + 1 + '').padStart(2, '0')) + '-' + ((this.date[1].getDate() + '').padStart(2, '0'))
      this.getHistoyList()
    },
    // 
    clearSearch() {
      this.date = ''
      // 
      this.queryInfo.startDate = ''
      this.queryInfo.endDate = ''
      this.getHistoyList()
    },
    // 
    async downloadPdf(obj) {
      const { data: res } = await this.$http.post('/qtzj/printCertificate', obj)
      console.log('',res)
      // 1. toBlobbase64Blob
      let fileURL = window.URL.createObjectURL(toBlob(res.data, 'pdf'))
      // pdf
      window.open(fileURL)

      // 2. 
      let link = document.createElement('a')  //
      link.style.display = 'none'   // 
      link.href = fileURL  // 
      //  a href+download download
      link.setAttribute('download',  ''+ '.pdf')
      document.body.appendChild(link) // 
      link.click()  // download
      document.body.removeChild(link) // 
      window.URL.revokeObjectURL(fileURL) // blob
    },
    async editApply(obj) {
      // obj
      this.editForm = obj
      // 
      const { data: { data: { imageType } } } = await this.$http.get('/qtzj/queryDetailZjqrlist', { params: this.editForm })
      // imageType=,
      if ( imageType !== '' ) this.isUpload = true
      // 
      const {data: { data }} = await this.$http.get('/qtzj/fetchImportList', { params: {
          applicationType: this.editForm.sqtype,
          applicationDate: this.editForm.sqrq,
          gjjzh: window.sessionStorage.getItem('gjjzh')
        }})
      // Imports
      this.fileInformation = data
      console.log(this.fileInformation)
      this.editDialogVisible = true
    },
    async btnOk() {
      const {data: {code}} = await this.$http.post('/qtzj/modifyZjqrlist', this.editForm)
      if (code !==200) return this.$message.error('')
      this.$message.success('')
      this.editDialogVisible = false
      // 
      this.getHistoyList()
    },
    btnCancel() {
      this.$refs.editFormRef.resetFields()
    },
    // 
    async submitUpload(fileType,index) {
      fileType = fileType.split(',')
      console.log(index)
      // 
      const ref = 'uploadRef' + index
      if (this.$refs[ref].uploadFiles.length === 0) {
        // 
        return this.$message.warning('')
      }
      // 1. ,rawFile
      const file = this.$refs[ref].uploadFiles[0].raw
      console.log(file)
      // 
      const symbolIndex = file.name.lastIndexOf('.') // .
      const realType = file.name.substr(symbolIndex + 1) // 
      if (fileType.indexOf(realType) === -1) {
        this.$refs[ref].uploadFiles = []
        return this.$alert('', '', {
          confirmButtonText: ''
        })
      }
      let fd = new FormData()
      fd.append('file', file)
      // 2. 
      const tempUploadfileInfo =  JSON.stringify({
        applicationDate: this.editForm.sqrq,
        applicationType: this.editForm.sqtype,
        businessId: this.fileInformation.Busiid,
        businessName: this.fileInformation.BusiName,
        gjjzh: sessionStorage.getItem('gjjzh'),
        ImUUid: this.fileInformation.Imports[index].ImUUid,
        ImportName: this.fileInformation.Imports[index].ImportName,
        FileName: file.name
      })
      fd.append('qtzjFileInfoDTO', new Blob([tempUploadfileInfo], {type: "application/json"}))
      // 3. 
      const { data: res }  = await this.$http.post('/qtzj/uploadFile', fd)
      if (res.code !== 200) {
        this.$message.error(res.msg)
      } else {
        // 
        this.fileInformation.Imports[index].IsUpload = true
      }
    },
    handleExceed() {
      this.$message.warning('')
    },
    beforeRemove(file, fileList) {
      return this.$confirm(` ${ file.name }`);
    },
    // 
    async preview(index) {
      //  
      this.previewUrlList = []
      const { data: { data: imageInfoList } } = await this.$http.get('/qtzj/downloadFile', {
        params: {
          applicationDate: this.editForm.sqrq,
          applicationType: this.editForm.sqtype,
          gjjzh: sessionStorage.getItem('gjjzh'),
          imUuid: this.fileInformation.Imports[index].ImUUid,
        }
      })
      console.log(imageInfoList)
      // 
      // 
      this.previewUrl = window.URL.createObjectURL(toBlob(imageInfoList[0].FileBase64, imageInfoList[0].FileType, true))
      imageInfoList.forEach(item => {
        let url = window.URL.createObjectURL(toBlob(imageInfoList[0].FileBase64, imageInfoList[0].FileType, true))
        this.previewUrlList.push(url)
      })
      this.previewIsShow = true
    }
  }
}
</script>

<style lang="less" >
  .worker-item .el-form-item__label{
    font-weight: bold;
    font-size: 16px;
  }
  .el-dialog__body {
    padding-top: 0;
  }
  h1 {
    margin-top: 5px;
  }
  .prePic {
    position: fixed;
    top: 40%;
    left: 60%;
    transform: translate(-50%, -50%);
    z-index: 2007;

    &::before {
      content: "";
      display: block;
      text-align: center;
      font-weight: bold;
      color: red;
    }

    .el-icon-close {
      border: 1px solid #373d41;
      border-radius: 6px;
      position: absolute;
      font-size: 12px;
      top: 5px;
      right: 0;
      cursor: pointer;
    }
  }
</style>
